import { ElementRef, SimpleChange } from '@angular/core';
/**
 * @param {?} selector
 * @param {?} scrollWindow
 * @param {?} defaultElement
 * @param {?} fromRoot
 * @return {?}
 */
export function resolveContainerElement(
  selector: string | any,
  scrollWindow,
  defaultElement,
  fromRoot: boolean
): any {
  const /** @type {?} */ hasWindow = window && !!window.document;
  const /** @type {?} */ containerIsString =
    selector && hasWindow && typeof selector === 'string';
  let /** @type {?} */ container = hasWindow && scrollWindow ? window : defaultElement;
  if (containerIsString) {
    container = containerIsString
      ? findElement(selector, defaultElement.nativeElement, fromRoot)
      : selector;
  }
  return container;
}
/**
 * @param {?} selector
 * @param {?} customRoot
 * @param {?} fromRoot
 * @return {?}
 */
export function findElement(
  selector: string | any,
  customRoot: ElementRef | any,
  fromRoot: boolean
) {
  const /** @type {?} */ rootEl = fromRoot ? window.document : customRoot;
  return rootEl.querySelector(selector);
}
/**
 * @param {?} prop
 * @return {?}
 */
export function inputPropChanged(prop: SimpleChange): boolean {
  return prop && !prop.firstChange;
}
/**
 * @return {?}
 */
export function hasWindowDefined(): boolean {
  return typeof window !== 'undefined';
}
