import { InfiniteScrollEvent, IPositionStats } from '../models';

export interface IScrollerProps {
  container: IPositionStats;
  down: number;
  up: number;
  alwaysCallback: boolean;
  disabled: boolean;
}

export interface ITriggerEvents {
  down: (event: any) => any;
  up: (event: any) => any;
}

export interface IDistanceRange {
  down: number;
  up: number;
}

export interface IScrollConfig {
  alwaysCallback: boolean;
  disable: boolean;
  shouldScroll: boolean;
}
/**
 * @param {?} __0
 * @return {?}
 */
export function shouldTriggerEvents({
  alwaysCallback,
  shouldScroll,
  disable
}: IScrollConfig) {
  return (alwaysCallback || shouldScroll) && !disable;
}
/**
 * @param {?} callbacks
 * @param {?} isScrollingDown
 * @param {?} scrolledUntilNow
 * @return {?}
 */
export function triggerEvents(
  callbacks: ITriggerEvents,
  isScrollingDown: boolean,
  scrolledUntilNow: number
) {
  const /** @type {?} */ eventData: InfiniteScrollEvent = {
    currentScrollPosition: scrolledUntilNow
  };
  const /** @type {?} */ callback = isScrollingDown ? callbacks.down : callbacks.up;
  callback(eventData);
}
